<template>
    <div class="main">
        <div class="operationLog">
            <div class="searchA">
                <el-date-picker v-model="searchForm.date" value-format="yyyy-MM-dd HH:mm:ss" type="datetimerange" size="small" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"> </el-date-picker>
                <el-select v-model="searchForm.region_id" filterable placeholder="请选择区域" size="small" style="margin-left: 25px;" @change="SelectChange(region_id)" clearable>
                    <el-option v-for="item in regionData" :key="item.id" :label="item.value" :value="item.id"> </el-option>
                </el-select>
                <el-input v-model="searchForm.keyword" placeholder="输入学生姓名、备注" size="small" clearable></el-input>
                <el-button size="small" type="primary" plain icon="el-icon-search" @click="searchClick" style="margin-left: 25px;">查询</el-button>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :width="item.width" :show-overflow-tooltip="true" v-if="item.prop == 'model_action_text'">
                            <template slot-scope="scope">
                                <span v-if="scope.row.model_action == 'insert'" style="color: #67C23A;">{{ scope.row.model_action_text }}</span>
                                <span v-else-if="scope.row.model_action == 'update'" style="color: #E6A23C;">{{ scope.row.model_action_text }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :width="item.width" :show-overflow-tooltip="true" v-else></el-table-column>
                    </template>
                </el-table>
            </div>
            <!-- <div class="pagination"> -->
            <el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />

        </div>
    </div>
</template>
<script>
import { operateLogsList, resArraysList } from '@/api/index';
export default {
    data() {
        return {
            total: 0,
            page: 1,
            pageSize: 15,
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            tableHead: [
                { title: '编号', prop: 'id', width: '120' },
                { title: '管理员姓名', prop: 'real_name', width: '150' },
                { title: '学生姓名', prop: 'child_name', width: '150' },
                { title: '学校类型', prop: 'school_type_name', width: '100' },
                // { title: '数据表', prop: 'table_name', width: '100'},
                { title: '操作节点', prop: 'operation', width: '300' },
                { title: '备注', prop: 'remark', },
                { title: '操作时间', prop: 'create_time', width: '200' },
            ],
            region_id: '',
            tableData: [],
            searchForm: {
                keyword: '',
                region_id: '',
                date: [],
                controller_name: '',
                method_name: '',
            },
            operaId: '',
            resArray: [],
            regionData: [],
        };
    },
    mounted() {
        this.getResArrayList();
        this.getList();
    },
    methods: {
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.getList();
        },
        searchClick() {
            if (this.operaId == '') {
                this.searchForm.controller_name = this.searchForm.method_name = ''
            }
            if (this.searchForm.date == null) {
                this.searchForm.date = [];
            }
            this.tableData = [];
            this.pageSize = 15;
            this.page = 1;
            this.getList();
        },
        async getList() {
            this.isLoading = true;
            let params = {
                keyword: this.searchForm.keyword,
                start_time: this.searchForm.date[0] ? this.searchForm.date[0] : '',
                end_time: this.searchForm.date[1] ? this.searchForm.date[1] : '',
                region_id: this.searchForm.region_id,
                controller_name: this.searchForm.controller_name,
                method_name: this.searchForm.method_name,
                curr: this.page,
                pagesize: this.pageSize,
            }
            const { data } = await this.axios.post(operateLogsList, params);
            if (data.code == 1) {
                this.isLoading = false;
                this.tableData = data.data.data;
                this.total = data.data.total;
            } else {
                this.isLoading = false;
                this.$message({
                    showClose: true,
                    message: data.msg,
                    type: 'error'
                });
            }
        },
        getRes(e) {
            this.resArray.map(ele => {
                if (ele.id == e) {
                    this.searchForm.controller_name = ele.controller;
                    this.searchForm.method_name = ele.method;
                }
            })
        },
        //切换区县下拉列表
        SelectChange(e) {
            this.region_id = e;
            this.getList()
        },
        async getResArrayList() {
            const { data } = await this.axios.post(resArraysList, {});
            if (data.code == 1) {
                this.resArray = data.data;
                this.regionData = data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: data.msg,
                    type: 'error'
                });
            }
        }
    },
};
</script>

<style lang="scss" scoped>
.operationLog {
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
    }

    .dialogInput {
        display: flex;
        margin-bottom: 0px;
        .padd {
            width: 100%;
            margin-right: 20px;
            .paddTit {
                margin-bottom: 10px;
            }
        }
    }
    .btn {
        width: 250px;
    }
}
</style>



